.app{display:grid;grid-auto-columns:1fr;grid-template-columns:1fr;grid-template-rows:1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr 1fr;grid-gap:0px 0px;gap:0px 0px;grid-template-areas:"marginTop" "top" "top" "mid" "mid" "mid" "mid" "mid" "mid" "mid" "bot" "bot" "margin-bot";margin:0 auto;width:clamp(300px, 100%, 1000px);height:100%}.app__title{grid-area:top;text-align:left;padding-top:15px;display:-webkit-flex;display:flex}.app__title__text{color:#a6a6a6;font-family:"Roboto Mono", monospace;font-size:50px;font-weight:500;display:-webkit-flex;display:flex;cursor:pointer;transition:color 0.5s ease}.app__title__text:hover{color:#e6c300}.app__middle{grid-area:mid;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;padding:0px 5px}.app__middle__text{font-size:35px;color:gray;font-family:"Roboto Mono", monospace;letter-spacing:3px;transition:all 2s ease;word-break:break-word;word-wrap:nowrap}.app__middle__text__outgoing{color:#ccc}.app__middle__text__current{background-color:rgba(255,255,255,0.02);transition:background-color 0.5s ease;position:relative;border-bottom:2px solid #a8b2d1;-webkit-animation:blinker 2s ease infinite;animation:blinker 2s ease infinite}@-webkit-keyframes blinker{50%{border-color:transparent}}@keyframes blinker{50%{border-color:transparent}}.app__middle__text__incoming{color:gray}.summary{display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;width:100%;height:100%;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-family:"Roboto", sans-serif}.summary__data{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;width:clamp(280px, 100%, 800px);-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:space-between;align-items:space-between;padding:0px 5px}.summary__data__item{font-size:2rem;font-weight:300}.summary__data__item__value{font-size:2.5rem;font-weight:500;color:#e6c300}*{box-sizing:border-box}html,body,#root{width:100%;height:100%;margin:0;padding:0;background-color:#333538;color:silver;-webkit-font-smoothing:antialiased}

